/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import { forceCheck } from "react-lazyload";
import { connect } from "react-redux";
import styled from "styled-components";
import { Loading } from "../../components/Loading";
import { RecommendList } from "../../components/RecommendList";
import { Scroll } from "../../components/Scroll";
import { Slider } from "../../components/Slider";
import { actinCreators } from "./store";

const ContentContainer = styled.div`
  position: fixed;
  top: 90px;
  bottom: 0;
  width: 100%;
`;

let Recommend = ({
  bannerList,
  recommendList,
  loading,
  getBannerDataDispatch,
  getRecommendDataDispatch,
}) => {
  useEffect(() => {
    getBannerDataDispatch();
    getRecommendDataDispatch();
  }, []);

  const bannerListJS = bannerList ? bannerList.toJS() : [];
  const recommendListJS = recommendList ? recommendList.toJS() : [];
  return (
    <ContentContainer>
      {loading && <Loading></Loading>}
      <Scroll onScroll={forceCheck}>
        <div>
          <Slider bannerList={bannerListJS}></Slider>
          <RecommendList recommendList={recommendListJS}></RecommendList>
        </div>
      </Scroll>
    </ContentContainer>
  );
};

const mapStateToProps = (state) => {
  return {
    bannerList: state.recommend.get("bannerList"),
    recommendList: state.recommend.get("recommendList"),
    loading: state.recommend.get("loading"),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getBannerDataDispatch() {
      dispatch(actinCreators.getBannerList());
    },
    getRecommendDataDispatch() {
      dispatch(actinCreators.getRecommendList());
    },
  };
};

Recommend = connect(mapStateToProps, mapDispatchToProps)(React.memo(Recommend));

export { Recommend };
