import { fromJS } from "immutable";
import { hotSingerList, singerListReq } from "../../../apis/request";
import {
  CHANGE_ENTER_LOADING,
  CHANGE_PAGE_COUNT,
  CHANGE_PULL_DOWN_LOADING,
  CHANGE_PULL_UP_LOADING,
  CHANGE_SINGER_LIST,
} from "./constants";

export const changeSingerList = (data) => ({
  type: CHANGE_SINGER_LIST,
  data: fromJS(data),
});

export const changePageCount = (count) => ({
  type: CHANGE_PAGE_COUNT,
  data: count,
});

export const changeEnterLoading = (loading) => ({
  type: CHANGE_ENTER_LOADING,
  data: loading,
});

export const changePullUpLoading = (loading) => ({
  type: CHANGE_PULL_UP_LOADING,
  data: loading,
});

export const changePullDownLoading = (loading) => ({
  type: CHANGE_PULL_DOWN_LOADING,
  data: loading,
});

export const getHotSingerList = () => (dispatch) =>
  hotSingerList(0).then((res) => {
    const data = res.artists;
    dispatch(changeSingerList(data));
    dispatch(changeEnterLoading(false));
    dispatch(changePullUpLoading(false));
    dispatch(changePullDownLoading(false));
    dispatch(changePageCount(data.length));
  });

export const getSingerList = (cat, alpha) => (dispatch) => {
  singerListReq(cat, alpha, 0).then(({ artists }) => {
    dispatch(changeSingerList(artists));
    dispatch(changeEnterLoading(false));
    dispatch(changePullUpLoading(false));
    dispatch(changePullDownLoading(false));
    dispatch(changePageCount(artists.length));
  });
};

export const loadMoreHotSingerList = (cat, alpha) => (dispatch, getState) => {
  const pageCount = getState().singers.get("pageCount");
  const singerList = getState().singers.get("singerList").toJS();

  hotSingerList(pageCount).then((res) => {
    const data = [...singerList, ...res.artists];
    dispatch(changeSingerList(data));
    dispatch(changePullUpLoading(false));
    dispatch(changePullDownLoading(false));
    dispatch(changePageCount(data.length));
  });
};

export const loadMoreSingerList = (cat, alpha) => (dispatch, getState) => {
  const pageCount = getState().singers.get("pageCount");
  const singerList = getState().singers.get("singerList").toJS();

  singerListReq(cat, alpha, pageCount).then((res) => {
    const data = [...singerList, ...res.artists];
    dispatch(changeSingerList(data));
    dispatch(changePullUpLoading(false));
    dispatch(changePullDownLoading(false));
    dispatch(changePageCount(data.length));
  });
};
