import React, { useContext, useEffect } from "react";
import styled from "styled-components";
import { Horizen } from "../../components/HorizenItem";
import { alphaTypes, categoryTypes } from "./constant";
import LazyLoad, { forceCheck } from "react-lazyload";
import { Scroll } from "../../components/Scroll";
import { style } from "../../assets/style";
import {
  changePageCount,
  changePullDownLoading,
  changePullUpLoading,
  getHotSingerList,
  getSingerList,
  loadMoreHotSingerList,
  loadMoreSingerList,
} from "./store/actionCreators";
import { connect } from "react-redux";
import { Loading } from "../../components/Loading";
import { CategoryDataContext, CHANGE_ALPHA, CHANGE_CATEGORY } from "./data";

const NavContainer = styled.div`
  box-sizing: border-box;
  position: fixed;
  top: 95x;
  width: 100%;
  padding: 5px;
  overflow: hidden;
`;

const List = styled.div`
  display: flex;
  margin: auto;
  flex-direction: column;
  overflow: hidden;
  .title {
    margin: 10px 0 10px 10px;
    color: ${style["font-color-desc"]};
    font-size: ${style["font-size-s"]};
  }
`;

const ListItem = styled.div`
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  margin: 0 5px;
  padding: 5px 0;
  align-items: center;
  border-bottom: 1px solid ${style["border-color"]};
  .img-wrapper {
    margin-right: 20px;
    img {
      border-radius: 3px;
      width: 50px;
      height: 50px;
    }
  }
  .name {
    font-size: ${style["font-size-m"]};
    color: ${style["font-color-desc"]};
    font-weight: 500;
  }
`;

const SingerContainer = styled.div`
  position: fixed;
  top: 160px;
  left: 0;
  bottom: 0;
  overflow: hidden;
  width: 100%;
`;

const Singer = ({
  singerList,
  getHotSingerListDispatch,
  updateDispatch,
  pullDownLoading,
  pullUpLoading,
  pullUpDispatch,
  pullDownDispatch,
  enterLoading,
}) => {
  // const [category, setCategory] = useState("");
  // const [alpha, setAlpha] = useState("");

  const { data, dispatch } = useContext(CategoryDataContext);
  const { category, alpha } = data.toJS();

  const singerListJS = singerList ? singerList.toJS() : [];

  useEffect(() => {
    if (category || alpha) {
      updateDispatch(category, alpha);
    } else {
      getHotSingerListDispatch();
    }
  }, [alpha, category, getHotSingerListDispatch, updateDispatch]);

  const handleCatClick = (value) => {
    // setCategory(value);
    dispatch({
      type: CHANGE_CATEGORY,
      data: value,
    });
  };

  const handleAlphaClick = (value) => {
    // setAlpha(value);
    dispatch({
      type: CHANGE_ALPHA,
      data: value,
    });
  };

  return (
    <NavContainer>
      {enterLoading && <Loading></Loading>}
      <Horizen
        list={categoryTypes}
        value={category}
        title="分类(默认热门):"
        handleClick={(value) => handleCatClick(value)}
      ></Horizen>
      <Horizen
        list={alphaTypes}
        title="首字母: "
        value={alpha}
        handleClick={(value) => handleAlphaClick(value)}
      ></Horizen>
      <SingerContainer>
        <Scroll direction="vertical">
          <Scroll
            onScroll={forceCheck}
            pullUpLoading={pullUpLoading}
            pullDownLoading={pullDownLoading}
            pullUp={() => pullUpDispatch(category, alpha)}
            pullDown={() => pullDownDispatch(category, alpha)}
          >
            <div>
              <List>
                {singerListJS.map((singer) => {
                  return (
                    <ListItem
                      key={singer.accountId + singer.id + singer.picUrl}
                    >
                      <div className="img-wrapper">
                        <LazyLoad
                          placeholder={
                            <img
                              src="https://p1.music.126.net/hSrYgWVp_LKnE2EPZdt_dQ==/109951165779590359.jpg"
                              alt="歌手图片"
                              width="100%"
                              height="100%"
                            />
                          }
                        >
                          <img
                            src={singer.picUrl}
                            alt="歌手图片"
                            width="100%"
                            height="100%"
                          />
                        </LazyLoad>
                      </div>
                      <div className="name">{singer.name}</div>
                    </ListItem>
                  );
                })}
              </List>
            </div>
          </Scroll>
        </Scroll>
      </SingerContainer>
    </NavContainer>
  );
};

const mapStatetoProps = (state) => ({
  singerList: state.singers.get("singerList"),
  enterLoading: state.singers.get("enterLoading"),
  pullUpLoading: state.singers.get("pullUpLoading"),
  pullDownLoading: state.singers.get("pullDownLoading"),
  pageCount: state.singers.get("pageCount"),
});

const mapDispatchToProps = (dispatch) => ({
  getHotSingerListDispatch() {
    dispatch(getHotSingerList());
  },
  updateDispatch(cat, alpha) {
    dispatch(changePageCount(0));
    dispatch(getSingerList(cat, alpha));
  },
  pullUpDispatch(cat, alpha) {
    dispatch(changePullUpLoading(true));
    if (cat || alpha) {
      dispatch(loadMoreSingerList(cat, alpha));
    } else {
      dispatch(loadMoreHotSingerList());
    }
  },
  pullDownDispatch(cat, alpha) {
    dispatch(changePullDownLoading(true));
    if (cat || alpha) {
      dispatch(getSingerList(cat, alpha));
    } else {
      dispatch(getHotSingerList());
    }
  },
});

const out = connect(mapStatetoProps, mapDispatchToProps)(React.memo(Singer));
export { out as Singers };
