import { instance } from "./config";

export const bannerListReq = () => instance.get("/banner");
export const recommendListReq = () => instance.get("/personalized");
export const singerListReq = (cat, initial, offset) =>
  instance.get(`/artist/list?area=${cat}&initial=${initial}&offset=${offset}`);

export const hotSingerList = (count) =>
  instance.get(`/top/artists?offset=${count}`);

export const getRankListReq = () => instance.get("/toplist/detail");
