import { memo, useEffect, useRef } from "react";
import Proptypes from "prop-types";
import styled from "styled-components";
import { style } from "../../assets/style";
import { Scroll } from "../Scroll";

const ListContainer = styled.div`
  display: flex;
  align-items: center;
  height: 30px;
  overflow: hidden;
  > span:first-of-type {
    display: block;
    flex: 0 0 auto;
    padding: 5px 0;
    margin-right: 5px;
    color: grey;
    font-size: ${style["font-size-m"]};
    vertical-align: middle;
  }
`;
const ListItem = styled.span`
  flex: 0 0 auto;
  font-size: ${style["font-size-m"]};
  padding: 5px 8px;
  border-radius: 10px;
  border: 1px solid transparent;
  &.selected {
    color: ${style["theme-color"]};
    border: 1px solid ${style["theme-color"]};
    opacity: 0.8;
  }
`;

const Horizen = ({ title, list, value, handleClick }) => {
  let categoryRef = useRef(null);

  useEffect(() => {
    let categoryDom = categoryRef.current;
    let tagElems = categoryDom.querySelectorAll("span");
    let totalWidth = 0;
    Array.from(tagElems).forEach((tagElem) => {
      totalWidth += tagElem.offsetWidth;
    });
    categoryDom.style.width = totalWidth + "px";
  }, []);

  return (
    <Scroll direction="horizental">
      <div ref={categoryRef}>
        <ListContainer>
          <span>{title}</span>
          {list.map((item) => {
            return (
              <ListItem
                key={item.key}
                className={`${value === item.key ? "selected" : ""}`}
                onClick={() => handleClick(item.key)}
              >
                {item.name}
              </ListItem>
            );
          })}
        </ListContainer>
      </div>
    </Scroll>
  );
};

Horizen.propTypes = {
  list: Proptypes.array,
  value: Proptypes.string,
  title: Proptypes.string,
  handleClick: Proptypes.func,
};

Horizen.defaultProps = {
  list: [],
  value: "",
  title: "",
  handleClick: null,
};

const memoHorizen = memo(Horizen);

export { memoHorizen as Horizen };
